<template>
  <!-- 呼朋唤友记录 -->
  <div class="page">
    <div class="bg">
      <img src="~@/assets/invite/bg@2x.png" alt="" />
    </div>
    <div class="main">
      <div class="bgbottom justify-center">
        <div class="bgtop">
          <div class="sort-wrap">
            <div class="sort-title">
              <div class="sort-name sort-text align-center">用户</div>
              <div class="invite-count sort-text align-center">唤友数</div>
            </div>
            <div
              class="sort-box"
              v-infinite-scroll="loadMore"
              infinite-scroll-disabled="loading"
              infinite-scroll-distance="20"
            >
              <div class="sorti" v-for="(sort, index) in dataList" :key="index">
                <div class="sort-name sort-text align-center">
                  {{ sort.fund_account }}
                </div>
                <div class="invite-count sort-text align-center">
                  {{ sort.create_time }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      page_size: 1,
      page_num: 1,
      is_last_page: false,
      loading: false,
      dataList: [
        // {
        //   mobile: '15512345678',
        //   fund_account: '15512345678',
        //   create_time: '2021-09-13 12:22:36'
        // }
      ]
    }
  },
  components: {
  },
  created () {
    this.getList(this.page_num)
  },
  methods: {
    // 获取列表
    async getList (page) {
      let res = await this.$request('get', 'user/getinvitepagelist', { page_num: page, page_size: this.page_size, uid: JSON.parse(localStorage.getItem('userInfo')).data.uid })
      if (res && res.error_no === '0') {
        if (this.page_num === 1) {
          this.dataList = res.data.list
        } else {
          this.dataList = this.dataList.concat(res.data.list)
        }
        this.is_last_page = res.data.is_last_page
        this.loading = false
      }
    },
    // 加载更多
    loadMore () {
      if (!this.is_last_page) {
        this.loading = true
        this.page_num += 1
        this.getList(this.page_num)
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  min-height: 100%;
  width: 100%;
  background-color: #e77f6b;
  overflow-y: hidden;
  position: relative;
  .bg {
    img {
      width: 100%;
    }
  }
  .main {
    z-index: 2;
    width: 100%;
    height: calc(100% - 98px);
    position: absolute;
    bottom: 26px;
    margin-top: 32px;
    .flex_func(column, center,center,nowrap);

    .bgbottom {
      width: calc(100% - 64px);
      min-height: 400px;
      border-radius: 20px;
      background: url(~@/assets/home/sort_cont_bg2@2x.png) no-repeat;
      background-size: 100% 100%;
      background-position: center;
      padding: 0 0 13px;
      .bgtop {
        width: calc(100% - 32px);
        height: 100%;
        min-height: 900px;
        z-index: 2;
        border-radius: 20px;
        overflow: hidden;
      }
      .sort-wrap {
        width: 100%;
        height: 100%;
        border-radius: 16px;
        margin-top: 20px;
        .sort-title {
          width: 100%;
          height: 88px;
          line-height: 88px;
          padding: 0 32px;
          font-size: 28px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: #fff;
          .flex_func(row,space-between,center,nowrap);
          .sort-text {
            color: #fff;
          }
        }
        .sort-box {
          width: 100%;
          height: calc(100% - 88px);
          overflow-y: auto;
          padding: 0 32px;
          background: #fef0f2;
          border-radius: 16px;
          overflow-y: auto;
          .sorti {
            width: 100%;
            height: 96px;
            border-top: 2px solid #efefef;
            .flex_func(row,space-between,center,nowrap);
          }
        }
        .sort-name {
          width: 240px;
          height: 100%;
        }
        .invite-count {
          width: calc(100% - 240px);
          height: 100%;
        }
        .sort-text {
          font-size: 28px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #333333;
          text-align: left;
        }
      }
    }
    .box {
      width: 686px;
      height: 100%;
      padding: 32px;
      z-index: 2;
      background: #fef0f2;
      border-radius: 16px;
      font-size: 24px;
      font-family: PingFangSC-Regular, PingFang SC;
      color: #333333;
      line-height: 34px;
      p {
        line-height: 34px;
      }
      .pcenter {
        margin: 30px 0;
      }
    }
  }
}
</style>



